// https://github.com/ustbhuangyi/lyric-parser

type PlayState = 'PLAYING' | 'PAUSE';
type LyricHandler = (data: { lineNum: number; lyricText: string }) => void;

/**
 * 
 * () 
 */
const timeExp = /\[(\d{2,}):(\d{2})(?:\.(\d{2,3}))?]/g;

const tagRegMap: { [key: string]: string } = {
  // [] 
  title: 'ti',
  artist: 'ar',
  album: 'al',
  offset: 'offset',
  by: 'by'
};

const noop = () => undefined; // dummy 

class LyricParser {
  private lyric: string; // 
  private tags: { [key: string]: string }; // { ti: '' }
  private lines: { time: number; lyricText: string }[]; // 
  private handler: LyricHandler; // 
  private state: PlayState; // 
  private curNum!: number; // 
  private startStamp!: number; // 
  private pauseStamp!: number; // 
  private timer!: NodeJS.Timeout;

  constructor(lyric: string, handler: LyricHandler = noop) {
    this.lyric = lyric;
    this.tags = {};
    this.lines = [];
    this.handler = handler;
    this.state = 'PAUSE';
    this.init();
  }

  /**
   * 
   *
   * @param {number} startTime 
   * @param {boolean} skipLast 
   * @returns {void}
   */
  public play(startTime: number = 0, skipLast?: boolean): void {
    if (!this.lines.length) {
      return;
    }
    this.state = 'PLAYING';
    this.curNum = this.findCurNum(startTime);
    //  startTime  startStamp 
    this.startStamp = +new Date() - startTime;
    if (!skipLast) {
      this.callHandler(this.curNum - 1);
    }
    if (this.curNum < this.lines.length) {
      clearTimeout(this.timer);
      this.playRest();
    }
  }

  /**
   * 
   */
  public togglePlay(): void {
    const now = +new Date();
    if (this.state === 'PLAYING') {
      this.stop();
      this.pauseStamp = now;
    } else {
      this.state = 'PLAYING';
      this.play((this.pauseStamp || now) - (this.startStamp || now), true);
      this.pauseStamp = 0;
    }
  }

  /**
   * 
   */
  public stop(): void {
    this.state = 'PAUSE';
    clearTimeout(this.timer);
  }

  /**
   *  offset 
   *
   * @param {number} offset
   * @returns {void}
   */
  public seek(offset: number = 0): void {
    this.play(offset);
  }

  private init(): void {
    this.initTag();
    this.initLines();
  }

  private initTag(): void {
    for (const tag in tagRegMap) {
      if (tagRegMap.hasOwnProperty(tag)) {
        // 
        const matches = this.lyric.match(
          new RegExp(`\\[${tagRegMap[tag]}:([^\\]]*)]`, 'i')
        );
        this.tags[tag] = (matches && matches[1]) || '';
      }
    }
  }

  private initLines(): void {
    const lines = this.lyric.split('\n');
    for (const line of lines) {
      const result = timeExp.exec(line);
      if (result) {
        const lyricText = line.replace(timeExp, '').trim();
        if (lyricText) {
          this.lines.push({
            time:
              +result[1] * 60 * 1000 +
              +result[2] * 1000 +
              (+result[3] || 0) * 10,
            lyricText
          });
        }
      }
    }
    this.lines.sort((a, b) => {
      // 
      return a.time - b.time;
    });
  }

  /**
   * 
   *
   * @param {number} time 
   * @returns {number}
   */
  private findCurNum(time: number): number {
    for (let i = 0; i < this.lines.length; i++) {
      if (time <= this.lines[i].time) {
        return i;
      }
    }
    return this.lines.length - 1;
  }

  /**
   * 
   *
   * @param {number} i 
   * @returns {void}
   */
  private callHandler(i: number): void {
    if (i < 0) {
      return;
    }
    this.handler({ lineNum: i, lyricText: this.lines[i].lyricText });
  }

  private playRest(): void {
    const line = this.lines[this.curNum];
    const delay = line.time - (+new Date() - this.startStamp);
    this.timer = setTimeout(() => {
      this.callHandler(this.curNum++);
      if (this.curNum < this.lines.length && this.state === 'PLAYING') {
        this.playRest();
      }
    }, delay);
  }
}

export default LyricParser;
